.timeline{
  position:relative;
  /* margin:40px auto; */
  margin-top: 40px;
  padding:1px 0;
  width: 100%;
  box-sizing:border-box;
}
.timeline:before{
  content:'';
  position:absolute;
  left:50%;
  width:2px;
  height:100%;
  background:#f5f4f3;
}
.timeline ul{
  padding:0;
  margin:0;
}
.timeline ul li{
  list-style:none;
  position:relative;
  width:50%;
  padding:20px 40px;
  box-sizing:border-box;
}
.timeline ul li:nth-child(odd){
  float:left;
  text-align:right;
  clear:both;
}
.timeline ul li:nth-child(even){
  float:right;
  text-align:left;
  clear:both;
}
.content{
  padding-bottom:20px;
}
.timeline ul li:nth-child(odd):before
{
  content:'';
  position:absolute;
  width:10px;
  height:10px;
  top:24px;
  right:-6px;
  background:#e65100;
  border-radius:50%;
  box-shadow:0 0 0 3px rgba(243, 129, 63, 0.2);
}
.timeline ul li:nth-child(even):before
{
  content:'';
  position:absolute;
  width:10px;
  height:10px;
  top:24px;
  left:-4px;
  background:#e65100;
  border-radius:50%;
  box-shadow:0 0 0 3px rgba(243, 129, 63, 0.2);
}
.timeline ul li h3{
  padding:0;
  margin:0;
  color: #80cbc3;
  font-weight:400;
}
.project-info {
  padding: 0;
  margin: 0;
  margin-top: 10px;
  /* color: #9db7c4; */
  font-weight:300;
}
.timeline ul li p{
  margin:10px 0 0;
  padding:0;
}
.timeline ul li .time h4{
  margin:0;
  padding:0;
  font-size:14px;
}
.timeline ul li:nth-child(odd) .time
{
  position:absolute;
  top:12px;
  right:-125px;
  margin:0;
  padding:8px 16px;
  background:#01796a;
  color:#fff;
  border-radius:18px;
  box-shadow:0 0 0 3px rgba(191, 212, 217, 0.1);
}
.timeline ul li:nth-child(even) .time
{
  position:absolute;
  top:12px;
  left:-125px;
  margin:0;
  padding:8px 16px;
  background:#01796a;
  color:#fff;
  border-radius:18px;
  box-shadow:0 0 0 3px rgba(191, 212, 217, 0.1);
}
@media(max-width:1000px)
{
  .timeline{
    width:100%;
  }
}
@media(max-width:767px){
  .timeline{
    width:100%;
    padding-bottom:0;
  }
  h1{
    font-size:40px;
    text-align:center;
  }
  .timeline:before{
    left:20px;
    height:100%;
  }
  .timeline ul li:nth-child(odd),
  .timeline ul li:nth-child(even)
  {
    width:100%;
    text-align:left;
    padding-left:50px;
    padding-bottom:50px;
  }
  .timeline ul li:nth-child(odd):before,
  .timeline ul li:nth-child(even):before
  {
    top:-18px;
    left:16px;
  }
  .timeline ul li:nth-child(odd) .time,
  .timeline ul li:nth-child(even) .time{
    top:-30px;
    left:50px;
    right:inherit;
  }
}

@media only screen and (max-width: 600px) {
  .project-content-m {
    display: none;
  }
}

@media (max-width: 992px) and (min-width: 601px) {
  .project-content-s-l {
    display: none;
  } 
}

@media (min-width:993px) {
  .project-content-m {
    display: none;
  }
}

